import parser from "@/components/jyf-Parser/index"
export default {
	onLoad(options) {
		console.log(options)
		this.orderId = options.orderId
		this.initRichText()
	},
	data () {
		return {
			orderId: '',
			cancelPerson: '',
			cancelRichText: '',
			isComplaint: -1,
			cancelList: ['信息填写错误,重新下单','临时有事，我不需要了','其他原因']
		}
	},
	components:{
		parser
	},
	methods:{
		async cancelSubmit () {
			if (this.cancelPerson.trim() == "") {
				uni.showToast({
					icon: 'none',
					title: '请填写取消订单的原因'
				})
				return
			}
			let res = await this.$fetch(this.$api.orderCancel,{orderId:this.orderId,cancelRemark:this.cancelPerson},"POST","Form")
			console.log(res)
			if (res.code == 0) {
				this.cancelPerson = ""
				uni.showToast({
					icon: 'success',
					title: '取消订单成功'
				})
				uni.setStorageSync('userSelect',6) 
				setTimeout(() =>{
					uni.switchTab({
						url: '../order/order',
					})
				},1000)
				
			} else {
				uni.showToast({
					icon: 'none',
					title: res.msg
				})
			}
		},
		async initRichText () {
			let res = await this.$fetch(this.$api.articleType,{type:8},"Post","Form")
			
			if (res.code == 0) {
				this.cancelRichText = res.data[0].articleContent
			}
		},
		handleClick (index,item) {
			if (index != this.isComplaint) {
				this.isComplaint = index
			} else {
				this.isComplaint = -1
			}
			console.log(item)
			this.cancelPerson = item
		},
	}
}
